import {fetchPoll} from '@github-ui/fetch-utils'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {ready} from '@github-ui/document-ready'
import content_warnings from '../../../config/health/trust_safety/content_warnings.json'

interface ContentWarning {
  type: string
  label: string
  purpose: string
  title: string
  subtitle?: string
  sub_categories?: Record<string, {label: string; value: string}>
}
type ContentWarnings = Record<string, ContentWarning>

// e.g. https://github.com/stafftools/repositories/monalisa/smile/disable_job_info
const disableJobInfoPath = window.location.pathname.split('/').slice(0, -1).concat('disable_job_info').join('/')

/**
 * The page markup is constructed server-side. By the time it's rendered in the browser the disable job status
 * may have changed. If so, we reload the page to ensure the correct status is displayed in the UI.
 *
 * It's also possible that when the page is rendered the job is in a pending state. In this case we start
 * polling the job status and reload the page once it changes to 'success' or 'error'.
 */
;(async () => {
  await ready

  const jobState = await getDisableJobState()
  if (!jobState) return

  const uiState = document.querySelector('[data-disable-job-status]')?.getAttribute('data-disable-job-status')

  if (jobState !== uiState) {
    // The UI and job state are out of sync. Let's reload.
    window.location.reload()
  } else {
    // The job is pending. Reload once it's finished.
    if (['pending', 'started'].includes(jobState)) {
      await fetchPoll(disableJobInfoPath)
      window.location.reload()
    }
  }
})()

/****************************** Setup Listeners *******************************/

observe('#disable_access_modal[open]', () => {
  onOpenDisableAccessModal()
})

on('change', '#disable_access_modal select[name="reason"]', function (event) {
  const target = event.currentTarget as HTMLSelectElement
  onChangeReason(target.selectedOptions)
})

observe('#content_warning_apply[open]', updateContentWarningFormState)
on('change', '#content_warning_apply #apply_to_forks', updateContentWarningFormState)
on('change', '#content_warning_apply #category', updateContentWarningFormState)
on('change', '#content_warning_apply #sub_category', updateContentWarningFormState)
// eslint-disable-next-line delegated-events/no-high-freq
on('input', '#content_warning_apply #custom_sub_category', updateContentWarningFormState)

/************************* Helpers & Event Handlers ***************************/
function onChangeReason(selectedReasons: HTMLCollectionOf<HTMLOptionElement>) {
  if (selectedReasons.length !== 1) throw new Error(`Expected 1 reason, but got: ${selectedReasons.length} reasons`)
  const newReason = selectedReasons[0]!

  for (const field of getAllTemplateFields()) {
    const shouldShow = field.id.endsWith(newReason.value)
    field.hidden = !shouldShow
  }
  adjustHeights()

  // The "abuse at scale" reason is special because it's treated as "tos"
  // when the form is submitted, but changes "notify_fork_owners" to false.
  const checkBox = getDisableAccessFieldById<HTMLInputElement>('notify_fork_owners')!
  if (checkBox) checkBox.checked = newReason.label !== 'abuse at scale'
}

function onOpenDisableAccessModal() {
  adjustHeights()

  // Make sure text field for selected reason is shown
  const reasonSelector = getDisableAccessFieldById<HTMLSelectElement>('disable_reason')
  if (reasonSelector) onChangeReason(reasonSelector.selectedOptions)
}

// Adjust field heights to fit content
function adjustHeights() {
  for (const field of getAllTemplateFields()) {
    field.style.height = `${field.scrollHeight + 3}px`
  }
}

function getAllTemplateFields() {
  return document.getElementById('disable_access_modal')!.querySelectorAll<HTMLInputElement>('[id^="email_template"]')
}

async function getDisableJobState() {
  const res = await fetch(disableJobInfoPath, {headers: {'X-Requested-With': 'XMLHttpRequest'}})
  if (res.status === 404) return undefined
  if (res.status < 200 || res.status >= 300) throw new Error(`HTTP ${res.status}`)
  const json = await res.json()
  return json.job.state
}

function getDisableAccessFieldById<T extends HTMLElement>(id: string): T | null | undefined {
  return document.getElementById('disable_access_modal')?.querySelector<T>(`[id="${id}"]`)
}

function updateContentWarningFormState() {
  // Get element references
  const category = getContentWarningFieldById<HTMLSelectElement>('category')!

  const subCategory = getContentWarningFieldById<HTMLSelectElement>('sub_category')!
  const subCategoryContainer = getContentWarningFieldById('sub_category_container')!

  const customSubCategory = getContentWarningFieldById<HTMLSelectElement>('custom_sub_category')!
  const customSubCategoryContainer = getContentWarningFieldById('custom_sub_category_container')!

  const categoryExplanation = getContentWarningFieldById<HTMLParagraphElement>('category_explanation')!

  const previewLabel = getContentWarningFieldById<HTMLSpanElement>('preview_label')!

  const interstitialPreview = getContentWarningFieldById<HTMLHeadingElement>('interstitial_preview')!
  const interstitialPreviewTitle = interstitialPreview.querySelector('h2')!
  const interstitialPreviewSubtitle = interstitialPreview.querySelector('p')!

  const bannerPreview = getContentWarningFieldById<HTMLHeadingElement>('banner_preview')!
  const bannerPreviewTitle = bannerPreview.querySelector('.Banner-message p.Banner-title')!
  const bannerPreviewSubtitle = bannerPreview.querySelector('.Banner-message p:not(.Banner-title)')!

  const submitBtn = getContentWarningFieldById<HTMLButtonElement>('content_warning_submit')!

  // Update element states
  subCategoryContainer.hidden = category.value !== 'mis_dis_information'
  subCategory.required = !subCategoryContainer.hidden
  customSubCategoryContainer.hidden = category.value !== 'mis_dis_information' || subCategory.value !== 'custom'
  customSubCategory.required = !customSubCategoryContainer.hidden

  if (subCategoryContainer.hidden) subCategory.selectedIndex = -1
  if (customSubCategoryContainer.hidden) customSubCategory.value = ''

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const contentWarning = (content_warnings as any as ContentWarnings)[category.value]!
  const subTitleSuffix = contentWarning.sub_categories?.[subCategory.value]?.value || customSubCategory.value || ''

  categoryExplanation.textContent = contentWarning.purpose
  // eslint-disable-next-line i18n-text/no-en
  previewLabel.textContent = `Preview ${contentWarning.type}`

  bannerPreviewTitle.textContent = contentWarning.title
  bannerPreviewSubtitle.textContent = (contentWarning.subtitle || '') + subTitleSuffix

  interstitialPreviewTitle.textContent = contentWarning.title
  interstitialPreviewSubtitle.textContent = (contentWarning.subtitle || '') + subTitleSuffix

  bannerPreview.hidden = contentWarning.type !== 'banner'
  interstitialPreview.hidden = contentWarning.type !== 'interstitial'
  submitBtn.disabled =
    (subCategory.required && subCategory.selectedIndex < 0) ||
    (customSubCategory.required && customSubCategory.value === '')
}

function getContentWarningFieldById<T extends HTMLElement>(id: string): T | null | undefined {
  return document.getElementById('content_warning_apply')?.querySelector<T>(`[id="${id}"]`)
}
